import { _decorator, Component, Node, EditBox } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('SettingDetailController')
export class SettingDetailController extends Component {

	
	@property({type: EditBox})
 	public totalPlayed: EditBox|null = null;

 	@property({type: EditBox})
 	public entryLevel: EditBox|null = null;

 	@property({type: EditBox})
 	public ways: EditBox|null = null;

 	@property({type: EditBox})
 	public coinValue: EditBox|null = null;


    start() {
        
    }

    update(deltaTime: number) {
        
    }

    showAllValue() {
        this.totalPlayed.text = '122';
        this.entryLevel.text = '122';
        this.ways.text = '122';
        this.coinValue.text = '122';
    }

    showPanel() {
        this.node.active = true;
    }

    hidePanel() {
        this.node.active = false;
    }

    onChangeTotalPlayed(offset) {
        this.totalPlayed.text = this.totalPlayed.text/1 + (offset);
        console.log('-----', this.totalPlayed.text);
        this.showAllValue();
    }

}

